@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300&display=swap);
html, body {
    height: 100%;
    width:100%;
    font-family: 'Montserrat', sans-serif;
}
#root{
    height:100%;
    width:100%;
}

input[type="text"],input[type="password"] {

    background: #EEF1F8 0% 0% no-repeat padding-box;
    border-radius: 8px;

}

.card {
    box-shadow: 0px 3px 6px #00000029;
    border-radius: 12px;
    opacity: 1;
}
.container {
    background-color:#F4F4F6;
}
.btnColor {
    background-color:#5285EC;
}
.loginTitle{
    text-align: left;
    letter-spacing: 0;
    color: #537178;
    opacity: 1;
}


.chart-container{
    height:100px;
    width:100%;
}

.topcards {
    box-shadow: 0px 3px 6px #00000029;
    border-radius: 12px;
    opacity: 1;
}
.cardtitle{
    color: #537178;
    font-size:26px;
}
@media (min-width: 768px) {
    .topcards {
        min-height:158px;
        max-height:158px;
    }
}
.lineText {
    text-decoration: line-through;
    color: #8F9EA2;
    font-size:14px;
}
.textColor{
    color: #8F9EA2;
    font-size:14px;
}
.taskHeader{
    text-align: left;
    font-size: 24px;
    letter-spacing: 0px;
    color: #537178;
    opacity: 1;
}
.taskText{
    color:#5285EC;
    font-size:24px;
    text-decoration: line-through;
}
.taskColor{
    color:#5285EC;
    font-size:24px;
}
.btnColor {
    background-color:#5285EC;
}
.underline{
    border-bottom: 1px solid #E8E8E8;
}
.taskHeaderwithline{
    text-align: left;
    font-size: 24px;
    letter-spacing: 0px;
    color: #537178;
    opacity: 1;
    text-decoration: line-through;
}
.notaskCard{
    margin-top:150px;
}

